$mt-breakpoints: (
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px,
);

:host {
  --mt-quadro-pictograma-01-margin: 0;
  margin: var(--mt-quadro-pictograma-01-margin);

  padding: 1px;
  background-color: #ccdfdf;
  border: 1px solid #9abec0;
  display: block;
}

.mt-quadro-pictograma-01 {
  &__container {
    padding: 4px 8px;
    border: 1px solid #9abec0;
  }

  &__text {
    color: #455a5a;
    font-family: 'Merriweather', serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 1.5;
  }
}
